import React from 'react';
type Point = {
  x: number;
  y: number;
  time: number;
};

function distance(p1: Point, p2: Point) {
  return Math.hypot(p2.x - p1.x, p2.y - p1.y);
}

export function Game() {
  const [particles, setParticles] = React.useState<Array<Point>>([]);
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  var ctx = canvasRef?.current?.getContext('2d');

  React.useEffect(() => {
    if (ctx) {
      if (particles.length > 2) {
        const p1 = particles[particles.length - 1];
        const p2 = particles[particles.length - 2];
        const speed = distance(p1, p2) / (p1.time - p2.time);

        ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);

        for (let j = 0; j < speed / 2; j++) {
          ctx.beginPath();
          ctx.moveTo(particles[0].x, particles[0].y);
          for (let i = 1; i < particles.length; i++) {
            const p1 = particles[i];
            if (ctx) {
              ctx.strokeStyle = `hsl(${i * 3.6}, 70%, 50%, ${i / 100})`;
              ctx.lineTo(p1.x + j * 10, p1.y + j * 5);
              ctx.arc(p1.x + j * 10, p1.y + j * 5, 30, 20, Math.PI / 14);
            }
          }
          ctx.lineCap = 'round';
          ctx.lineWidth = 2;
          ctx.stroke();
          ctx.closePath();
          ctx.filter = 'blur(1px)';
        }
      }
    }
  }, [ctx, particles]);

  React.useEffect(() => {
    let interval = 0;

    setInterval(() => {
      //const newParticles = particles.slice(0);
      // newParticles.shift();
      //setParticles(newParticles);
    }, 2000);

    return () => {
      clearInterval(interval);
    };
  }, [particles]);
  React.useEffect(() => {
    if (canvasRef.current) {
      canvasRef.current.width = window.innerWidth;
      canvasRef.current.height = window.innerHeight;
    }
  }, [canvasRef]);

  React.useEffect(() => {
    const handler = (event: MouseEvent) => {
      const newParticles = particles.slice(0);
      newParticles.push({
        x: event.clientX,
        y: event.clientY,
        time: Date.now(),
      });
      if (newParticles.length > 80) {
        newParticles.shift();
      }
      setParticles(newParticles);
    };
    window.addEventListener('mousemove', handler);
    return () => {
      window.removeEventListener('mousemove', handler);
    };
  }, [particles]);
  return <canvas className="ball" ref={canvasRef}></canvas>;
}
